<template>
  <div class="ackDetails">
    <Details/>
    <BigButton :enable="enable" :content="buttonContent" :callBack="()=>{jumpFn(1)}"/>
  </div>
</template>

<script>
import Details from "./details";
import BigButton from "@/components/bigButton";
import store from "@/vuex/index";
export default {
  components: {
    Details,
    BigButton
  },
  created() {
    console.log(store.state.wmodule);
  },
  data() {
    return {
      enable: true,
      buttonContent: "确认信息"
    };
  },
  methods: {
    jumpFn() {
      store.commit('wmodule/changeBtnState',true)
      const {
        curFee,
        curHospitalId,
        curHospitalName,
        curDeptId,
        curDeptName,
        curDoctorId,
        curDoctorName,
        curAppointmentNumId,
        curDoctScheduleId
      } = store.state.wmodule;
      console.log(JSON.parse(sessionStorage.getItem("userInfo")));
      const userId = JSON.parse(sessionStorage.getItem("userInfo")).userId;
      const createUserId = JSON.parse(sessionStorage.getItem("userInfo")).userId;
      const updateUserId = JSON.parse(sessionStorage.getItem("userInfo")).userId;
      const sendAjaxAndJump = async ()=> {
        await store.dispatch("wmodule/getBillCode", {
              fee: curFee.toString(), // 需求为字符串格式，强制转为字符串
              hospId: curHospitalId,
              hospName: curHospitalName,
              deptId: curDeptId,
              deptName: curDeptName,
              doctId: curDoctorId,
              doctName:curDoctorName,
              userId:userId,
              createUserId:createUserId,
              updateUserId:updateUserId,
              id:curAppointmentNumId,
              doctorScheduleId:curDoctScheduleId
            });
        this.$router.push({ name: "ackPay"});
        console.log(store.state.wmodule)
      }
      sendAjaxAndJump();
    }
  }
};
</script>

<style scoped lang="less">
.ackDetails {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: unit(0.14*2,rem) unit(0.15*2,rem) 0 unit(0.15*2,rem);
  position: relative;
  // overflow: hidden;
  .bigButton {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: unit(0.44*2,rem);
    margin-bottom: unit(.49*2,rem);
  }
}
</style>



