<template>
  <div class="bigButton">
    <button
      class="enable"
      v-if="enable"
      @touchstart="!btnState && touchDown()"
      @touchend="!btnState && touchEnd()"
      @click="!btnState && callBack()"
      ref="button"
    >
      <span>{{content}}</span>
    </button>
    <button class="unable" v-else>
      <span>{{content}}</span>
    </button>
    <div class="busy" v-show="btnState"></div>
    <div class="mask" ref="mask"></div>
  </div>
</template>

<script>
import store from '@/vuex/index'
export default {
  name: 'bigButton',
  data () {
    return {
      btnState: store.state.wmodule.curBtnState
    }
  },
  props: ['enable', 'content', 'callBack'],
  computed: {},
  methods: {
    touchDown () {
      this.$refs.mask.style.display = 'block'
    },
    touchEnd () {
      this.$refs.mask.style.display = 'none'
    }
  }
}
</script>

<style scoped lang="less">
.bigButton {
  position: relative;
  width: 3.2 * 2rem;
  button {
    /*position: relative;*/
    padding: 0;
    border: 0;
    border-radius: 0.02 * 2rem;
    width: 3.2 * 2rem;
    height: 0.44 * 2rem;
    background: #3391e8;
    box-shadow: 0 0.04 * 2rem 0.06 * 2rem 0 rgba(45, 136, 219, 0.3);
    color: #fff;
    font-size: 0.18 * 2rem;
    letter-spacing: 0.04 * 2rem;
    font-family: PingFangSC-Regular, sans-serif;
    text-align: center;
    span {
      padding: 0.2rem 0;
      line-height: normal;
      letter-spacing: 0.08rem;
    }
  }

  .unable {
    background: #f1f1f1;
    box-shadow: none;
    color: #b1afaf;
  }
  .busy {
    position: absolute;
    top: 50%;
    margin-top: -0.075 * 2rem;
    background: url("../assets/But_busy.png") center no-repeat;
    background-size: 100%;
    width: 0.15 * 2rem;
    height: 0.15 * 2rem;
    right: 24%;
    animation: btn_busy_rotate 1s infinite;
  }

  .mask {
    display: none;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 3.2 * 2rem;
    height: 0.44 * 2rem;
    background-color: #000;
    opacity: 0.3;
    border-radius: 0.02 * 2rem;
  }
  @keyframes btn_busy_rotate {
    0% {
      transform: rotate(0deg);
    }
    10% {
      transform: rotate(36deg);
    }
    20% {
      transform: rotate(72deg);
    }
    30% {
      transform: rotate(108deg);
    }
    40% {
      transform: rotate(144deg);
    }
    50% {
      transform: rotate(180deg);
    }
    60% {
      transform: rotate(216deg);
    }
    70% {
      transform: rotate(252deg);
    }
    80% {
      transform: rotate(298deg);
    }
    90% {
      transform: rotate(324deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
</style>



