<template>
  <div class="details">
    <SecondaryTitleBar :title="appointmentTitle"/>
    <ul class="appointmentList">
      <li class="hospital">
        <span class="text">预约医院</span>
        <span class="content">{{hositalName}}</span>
      </li>
      <li class="department">
        <span class="text">预约科室</span>
        <span class="content">{{deptName}}</span>
      </li>
      <li class="doctor">
        <span class="text">预约医生</span>
        <span class="content">{{doctorName}}</span>
      </li>
      <li class="time">
        <span class="text">就诊时间</span>
        <span class="content">
          <span class="date">{{appointmentDate}}</span>
          <span class="week">{{appointmentWeek}}</span>
          <span class="hour">{{appointmentStartTime}}-{{appointmentEndTime}}</span>
          <!-- <span class="num">{{appointmentNum}}号</span> -->
        </span>
      </li>
      <li class="cost">
        <span class="text">挂号费</span>
        <span class="content">10.00元</span>
      </li>
    </ul>
    <SecondaryTitleBar :title="patientTitle"/>
    <ul class="patientList">
      <li class="patientName">
        <span class="text">患者姓名</span>
        <span class="content">{{patientName}}</span>
      </li>
      <li class="id">
        <span class="text">身份证号</span>
        <span class="content">{{idCard | idNumberFilter}}</span>
      </li>
      <li class="phoneNum">
        <span class="text">手机号码</span>
        <span class="content">{{telNum | telFilter}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import SecondaryTitleBar from "@/components/secondaryTitleBar";
import store from '@/vuex/index'
export default {
  name: "acketails",
  components: {
    SecondaryTitleBar
  },
  data() {
    return {
      appointmentTitle: "预约信息",
      patientTitle: "就诊人信息",
      hospitalId:store.state.wmodule.curHospitalId,// 医院id
      hositalName:store.state.wmodule.curHospitalName,// 医院名称
      deptId:store.state.wmodule.curDeptId,// 科室id
      deptName:store.state.wmodule.curDeptName,// 科室名称
      doctorId:store.state.wmodule.curDoctorId,// 医生id
      doctorName:store.state.wmodule.curDoctorName,// 医生名称
      appointmentDate:store.state.wmodule.curAppointmentDate,// 预约日期 2018-12-12
      appointmentStartTime:store.state.wmodule.curAppointmentStartTime,// 预约开始时间 13::55
      appointmentEndTime:store.state.wmodule.curAppointmentEndTime,// 预约结束时间 14::55
      appointmentWeek:store.state.wmodule.curAppointmentWeek,// 预约周几 周一
      appointmentNum:store.state.wmodule.curAppointmentNum,// 预约号码 22
      patientName: JSON.parse(sessionStorage.getItem('userInfo')).userName,
      idCard: JSON.parse(sessionStorage.getItem('userInfo')).idCard,
      telNum: JSON.parse(sessionStorage.getItem('userInfo')).phone
    };
  },
  filters: {
    idNumberFilter: function(myInput) {
      console.log(myInput);
      let result;
      if(myInput){
         var _leftNumber = myInput.substring(0, 6);
        var _rightNumber = myInput.substring(14, myInput.length);
        result = _leftNumber + "********" + _rightNumber;
      }else{
        result = ""
      }
      return result;
    },
    telFilter: function(myInput) {
      let result;
      if(myInput){
        var _leftNumber = myInput.toString().substring(0, 3);
        var _rightNumber = myInput.toString().substring(7, myInput.length);
        result = _leftNumber + "****" + _rightNumber;
      }else{
        result = ""
      }
      return result;
    }
  },
  mounted(){
   /*  console.log(JSON.parse(sessionStorage.getItem('userInfo')).userName)
    this.patientName = JSON.parse(sessionStorage.getItem('userInfo')).userName;
    this.idCard = JSON.parse(sessionStorage.getItem('userInfo')).idCard;
    this.telNum = JSON.parse(sessionStorage.getItem('userInfo')).phone; */
  }
};
</script>

<style scoped lang="less">
.details {
  width: 100%;
  box-sizing: border-box;
  background: #fff;
  box-shadow: unit(0.03*2,rem) unit(0*2,rem) unit(0.1*2,rem) unit(0.05*2,rem) rgba(51, 145, 232, 0.1);
  padding: 0 unit(0.14*2,rem) 0 unit(0.13*2,rem);

  .title {
    padding: 0;
  }

  .appointmentList {
    margin-bottom: unit(0.15*2,rem);
  }

  li {
    padding: unit(0.12*2,rem) 0 unit(0.11*2,rem) 0;
    border-bottom: unit(0.01*2,rem) solid #f6f7f8;
    overflow: hidden;

    .text {
      float: left;
      font-family: PingFangSC-Regular, sans-serif;
      font-size: unit(0.14*2,rem);
      line-height: unit(0.2*2,rem);
      color: #272727;
      opacity: 0.6;
    }

    .content {
      font-family: PingFangSC-Regular, sans-serif;
      font-size: unit(0.14*2,rem);
      color: #272727;
      float: right;
    }

    &.cost {
      .content {
        color: #ff941f;
      }
    }
  }
  .patientList {
    li {
      &:nth-last-of-type(1) {
        border-bottom: none;
      }
    }
  }
}
</style>



