<template>
  <div class="title">
    <span class="bar"></span>
    <span class="text">{{title}}</span>
  </div>
</template>

<script>
  export default {
    name: 'secondaryTitleBar',
    props: {
      title: String
    }
  }
</script>

<style scoped lang="less">
  .title {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    height: .427*2rem;
    line-height: .427*2rem;
    position: relative;
    padding-left: .15*2rem;

    .bar {
      position: absolute;
      display: inline-block;
      width: .03*2rem;
      height: .16*2rem;
      background: #3391E8;
      border-radius: 2px;
      top: 50%;
      transform: translateY(-50%);
    }

    .text {
      margin-left: .1*2rem;
      font-family: PingFangSC-Medium, sans-serif;
      color: #272727;
      font-size: .16*2rem;
      line-height: .22*2rem;
    }
  }
</style>



